@use '../module' as *;

.portfolio {
  background: $third-bck;
  padding: 2em 0;
  text-align: center;

  h1 {
    font-size: 1.8rem;
    font-weight: $fw-b;
    margin: 0;
    letter-spacing: 3px;
    text-transform: uppercase;

    @include breakpoint(tablet) {
      font-size: 2rem;
    }

    @include breakpoint(laptop) {
      font-size: 2.3rem;
    }

    @include breakpoint(large-l) {
      font-size: 3rem;
    }
  }

  .filters {
    padding: 3em 0 3em;
    button {
      background-color: rgba($dog-rose, 0.6);
      border: unset;
      display: inline-block;
      color: #fff;
      cursor: pointer;
      font-size: 0.7rem;
      font-weight: $fw-m;
      letter-spacing: 1px;
      margin: 0 0.3em 0.8em;
      padding: 0.8em;
      text-transform: uppercase;
      border-radius: 5px;

      box-shadow: 0px 0px 12px -2px rgba(0, 0, 0, 0.5);
      position: relative;
      overflow: hidden;
      transition: background-color 0.6s ease;

      @include breakpoint(tablet) {
        font-size: 0.8rem;
      }

      @include breakpoint(laptop) {
        font-size: 0.9rem;
      }

      &::after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        top: 50%;
        left: 50%;
        transform-style: flat;
        transform: translate3d(-50%, -50%, 0);
        background: rgba(#fff, 0.1);
        border-radius: 100%;
        transition: width 0.3s easem height 0.3s ease;
      }

      &:focus,
      &:hover {
        background: darken($dog-rose, 7%);
        outline: none;
      }

      &:active {
        &:after {
          width: 200px;
          height: 200px;
        }
      }
    }
  }

  .portfolio-items {
    padding: 5px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    display: grid;
    gap: 1.5em 2em;

    > div {
      position: relative;
      overflow: hidden;
      margin: 5px 0;

      @include breakpoint(tablet) {
        margin-left: 10px;
        margin-right: 10px;
      }

      @include breakpoint(laptop) {
        margin: 0 4.6em;
      }
    }

    .portfolio-item {
      position: relative;
      overflow: hidden;
      width: 100%;
      margin: 0;

      @include breakpoint(tablet) {
        max-width: 44vw;
      }

      @include breakpoint(laptop) {
        width: 34vw;
      }

      > img {
        display: block;
        min-height: 26vh;
        height: 100%;
        width: 100%;
        object-fit: contain;
      }

      figcaption {
        position: absolute;
        top: 100%;
        bottom: 0;
        right: 0;
        left: 0;
        padding: 1em;
        background: rgba(48, 35, 35, 0.7);
        color: #fff;
        text-align: left;
        display: flex;
        flex-direction: column;
        transition: top ease-in-out 250ms;
      }

      &:hover figcaption,
      &:focus figcaption {
        top: 0;
      }

      .portfolio-modal {
        position: fixed;
        z-index: 10;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        min-width: 259px;
        width: 80%;
        max-width: 870px;

        display: none;

        min-height: calc(80vh - 1em);
        max-height: calc(85vh - 2em);
        padding: 2em;
        background-color: #94d6d6;
        border-radius: 22px;

        &.is-open {
          display: block;
          @include breakpoint(tablet) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: 30px 40px 162px 110px auto;
            gap: 0 1em;
          }
        }
        > h2 {
          text-transform: uppercase;
          grid-column: 1;
          grid-row: 2 / 3;

          @include breakpoint(tablet) {
            margin: 0;
            padding: 0;
          }
        }

        .filters {
          grid-column: 1;
          grid-row: 5;

          @include breakpoint(tablet) {
            margin-top: -2.5em;
          }
        }

        &__image-box {
          grid-row: 3 / 5;
          max-height: calc(33vh - 1em);
          img {
            height: 100%;
            width: 100%;
            object-fit: contain;
          }
        }
        &__desc-container {
          grid-column: 2;
          grid-row: 2 / 3;

          .desc-box {
            min-height: 33vh;
            height: 36vh;
            max-height: calc(51vh - 2em);
            overflow-y: auto;
            @include breakpoint(tablet) {
              height: 47vh;
            }

            p {
              font-size: 1.1rem;
              letter-spacing: 1px;
              line-height: 1.4;
            }
          }

          > p {
            font-size: 1.2rem;
            font-weight: 600;
            letter-spacing: 2px;
            margin: 1em 0 0;
            text-transform: uppercase;
            @include breakpoint(tablet) {
              margin: 0;
              margin-top: 5px;
              margin-bottom: 5px;
            }
          }
        }
        .desc-buttons-box {
          margin-top: 2.5em;
          grid-column: 2;
          grid-row: 4;
          align-self: end;

          @include breakpoint(tablet) {
            // margin-top: -2.5em;
          }
          a {
            padding: 0.8em;
            background-color: #daaba6;
            border: unset;
            text-transform: uppercase;
            box-shadow: 0px 0px 12px -2px rgb(0 0 0 / 50%);
            transition: background-color 0.6s ease;
            cursor: pointer;
            border-radius: 5px;
            letter-spacing: 2px;

            > i::before {
              color: $charcoal-black;
            }
          }
          a:hover {
            background-color: #d6928b;
          }

          a:first-child {
            margin-right: 0.5em;
          }

          a:last-child {
            margin-left: 0.5em;
          }
        }

        .filters {
          padding: 0;
        }
      }
    }
  }

  .portfolio-title {
    font-size: 2rem;
    margin-bottom: auto;
  }

  .portfolio-link {
    color: #fff;
    font-weight: $fw-b;
    text-decoration: none;
    border-bottom: 2px solid #fff;
    align-self: flex-start;
    cursor: pointer;
  }
}
